/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import { Card, Skeleton, message } from "antd";
import CreateModal from "./CreateModal";
import { Typography, Row, Col, Icon } from "antd";
import { connect } from "react-redux";
import { getProjects } from "../../../action/project";
import { clearErr } from "../../../action/resource";
import MyModal from "../../util/WarnModal";
import { Link } from "react-router-dom";
import { textLimiter } from "../../../util/textLimiter";
import useTitle from "../../util/useTitle";

const { Title, Text } = Typography;
const { Meta } = Card;

const Projects = props => {
  const [visible, setVisible] = useState(false);
  useTitle("all projects");

  const { err } = props;
  useEffect(() => {
    if (Object.keys(props.err).length) {
      message.error("new props");
      MyModal({ data: props.err });
      props.clearErr();
    }
  }, [err]);

  useEffect(() => {
    props.getProjects();
  }, []);

  const MyCard = () => {
    if (typeof props.projects === "undefined" && props.loading) {
      return (
        <Col xs={24} sm={12} md={8} lg={6}>
          <Card
            onClick={() => setVisible(true)}
            hoverable
            style={{
              borderRadius: "12px",
              margin: "8px",
              height: "210px",
              overflow: "hidden",
              backgroundColor: "#fff"
            }}
          >
            <Skeleton active />
          </Card>
        </Col>
      );
    } else if (typeof props.projects === "undefined") {
      return (
        <Col xs={24} sm={12} md={12} lg={8} xl={6}>
          <Card
            onClick={() => setVisible(true)}
            hoverable
            style={{
              borderRadius: "12px",
              margin: "8px",
              height: "210px",
              overflow: "hidden",
              backgroundColor: "#fff"
            }}
          >
            <Title level={2}>
              <Text type="secondary">
                no project <br /> for add project please click on add card
              </Text>
            </Title>
          </Card>
        </Col>
      );
    }
    return props.projects.map(item => (
      <Col xs={24} sm={12} md={12} lg={8} xl={6} key={item.projectId}>
        <Link to={`/projects/${item.projectId}`}>
          <Card
            hoverable
            style={{
              borderRadius: "12px",
              margin: "8px",
              height: "210px",
              overflow: "hidden"
            }}
            cover={
              <Title
                style={{
                  color: "#303020",
                  padding: "16px",
                  margin: "0 auto",
                  textAlign: "center",
                  height: "120px"
                }}
                level={4}
              >
                {textLimiter(item.title, 14)}
              </Title>
            }
          >
            <Meta
              title={item.creator.name}
              description={textLimiter(item.description, 28)}
            />
          </Card>
        </Link>
      </Col>
    ));
  };

  return (
    <Row gutter={16}>
      <CreateModal visible={visible} setVisible={setVisible} />
      <Title
        style={{
          color: "#303020",
          paddingBottom: "16px",
          margin: "0 auto",
          textAlign: "center"
        }}
        level={3}
      >
        Your Porjects
      </Title>
      <Col xs={24} sm={12} md={12} lg={8} xl={6}>
        <Card
          onClick={() => setVisible(true)}
          hoverable
          style={{
            borderRadius: "12px",
            margin: "8px",
            height: "210px",
            overflow: "hidden",
            backgroundColor: "#f0f2f5"
          }}
          cover={
            <Icon
              type="plus-circle"
              theme="twoTone"
              twoToneColor="#d9d9d9"
              style={{
                fontSize: "10em",
                textAlign: "center",
                margin: " 35px auto",

                color: "#08c"
              }}
            />
          }
        />
      </Col>
      {MyCard()}
    </Row>
  );
};

const mapStateToProps = state => ({
  projects: state.projects,
  err: state.err,
  loading: state.loading
});

export default connect(
  mapStateToProps,
  { getProjects, clearErr }
)(Projects);
