import { Modal, Button } from "antd";
import { Input, InputNumber } from "antd";
import React, { useState, useEffect } from "react";
import { connect } from 'react-redux'
import { createProject } from "../../../action/project";
const CreateModal = (props) => {
	const [loading, setLoading] = useState(false);
	const [name, setName] = useState("");
	const [duration, setDuration] = useState(1);
	const [description, setDescription] = useState("");
	const [unvalidForm, setUnValidForm] = useState(true);

	const handleOk = () => {
		setLoading(true);
		console.log(description, name);
		props.createProject({ title: name, description, sprintDuration: duration })
		setLoading(false);
		props.setVisible(false);

	};

	useEffect(() => {
		if (name.length >= 3 && description.length >= 8) {
			setUnValidForm(false);
		} else {
			setUnValidForm(true);
		}
	}, [name, description]);

	return (
		<Modal
			visible={props.visible}
			title="Create Project"
			onCancel={() => props.setVisible(false)}
			footer={[
				<Button key="back" onClick={() => props.setVisible(false)}>
					Cancel
				</Button>,
				<Button
					disabled={unvalidForm}
					key="submit"
					type="primary"
					loading={loading}
					onClick={handleOk}
				>
					Create
				</Button>,
			]}
		>
			<Input
				onChange={(e) => setName(e.target.value)}
				placeholder="project title min length is 3 char"
				value={name}
			/>
			<br />
			<br />

			<Input.TextArea
				value={description}
				onChange={(e) => setDescription(e.target.value)}
				placeholder="project description min length is 8 char"
				autosize={{ minRows: 2, maxRows: 6 }}
			/>
			<InputNumber
				id="duration"
				style={{
					width: "60px"
				}}
				max={15}
				min={1}
				value={duration}
				defaultValue={duration}
				onChange={(val) => setDuration(val)}
			/>
		</Modal>
	);
};

export default connect(null, { createProject })(CreateModal);
